import {
  Proctoring,
  Action,
  ActionTypes,
  ActiveCandidateLiveServer,
} from "../actions";
import {
  CandidateProctoringInterface,
  ProctoringExam,
  RoomServer,
} from "../interfaces";
// default state
const defaultState = {
  events: [],
  room: {
    loaded: false,
    center_id: "",
    center_name: "",
    district_id: "",
    district_name: "",
    province_id: "",
    province_name: "",
    room_id: "",
    room_number: "",
  } as RoomServer,
  candidates: [],
  me: {
    active: false,
    email: "",
    fname: "",
    lname: "",
    stopped: false,
    user_id: "",
    muted: false,
    peer: "",
    peer_id: "",
    ref: "",
  } as CandidateProctoringInterface,
  room_exams: [],
  exam: {
    end_time: new Date().toDateString(),
    exam_done: false,
    start_exam: false,
    start_time: new Date().toDateString(),
    video_support: false,
    name: "this is true",
  } as ProctoringExam,
} as Proctoring;

/**
 * @param state
 * @param action
 * @returns
 */
export const proctoringReducer = (
  state: Proctoring = defaultState,
  action: Action
) => {
  switch (action.type) {
    case ActionTypes.ADD_ROOM_DETAILS:
      return {
        ...state,
        candidates: action.payload.candidates,
        room: { ...state.room, ...action.payload.room },
      } as Proctoring;
    case ActionTypes.ADD_ACTIVE_USER_TO_ROOM:
      return {
        ...state,
        candidates: state.candidates.map(
          (cand: CandidateProctoringInterface) => {
            // find the current candidate to the list of active candidates
            let candExists = action.payload.find(
              (itm: ActiveCandidateLiveServer) => itm.user_id === cand.user_id
            );
            return {
              ...cand,
              active: candExists === undefined ? false : true,
            } as CandidateProctoringInterface;
          }
        ),
      } as Proctoring;

    case ActionTypes.ADD_ONE_ACTIVE_USER_TO_ROOM:
      return {
        ...state,
        candidates: state.candidates.map((itm: CandidateProctoringInterface) =>
          itm.user_id === action.payload ? { ...itm, active: true } : itm
        ),
      } as Proctoring;
    case ActionTypes.CANDIDATE_OFFLINE:
      return {
        ...state,
        candidates: state.candidates.map((itm: CandidateProctoringInterface) =>
          itm.user_id === action.payload ? { ...itm, active: false } : itm
        ),
      } as Proctoring;
    case ActionTypes.EXAM_START_STOP:
      return {
        ...state,
        room: {
          ...state.room,
          start_exam: action.payload,
        },
      } as Proctoring;
    case ActionTypes.CANDIDATE_CONTINUE_EXAM:
      return {
        ...state,
        candidates: state.candidates.map((cand: CandidateProctoringInterface) =>
          cand.user_id === action.payload ? { ...cand, stopped: false } : cand
        ),
      } as Proctoring;
    case ActionTypes.CANDIDATE_STOP_EXAM:
      return {
        ...state,
        candidates: state.candidates.map((cand: CandidateProctoringInterface) =>
          cand.user_id === action.payload ? { ...cand, stopped: true } : cand
        ),
      } as Proctoring;

    case ActionTypes.CANDIDATE_EXAM_EVENT:
      return {
        ...state,
        candidates: state.candidates.map((cand: CandidateProctoringInterface) =>
          cand.user_id === action.payload.user_id
            ? { ...cand, stopped: true }
            : cand
        ),
        events: [...state.events, action.payload],
      } as Proctoring;
    case ActionTypes.ADD_MY_DATA_TO_PROCTORING:
      return {
        ...state,
        me: action.payload as CandidateProctoringInterface,
      } as Proctoring;
    case ActionTypes.ME_START_STOP_EXAM:
      return {
        ...state,
        room: {
          ...state.room,
          start_exam: action.payload,
          exam_done: !action.payload,
        },
      } as Proctoring;
    case ActionTypes.BLOCK_EXAM:
      return {
        ...state,
        me: {
          ...state.me,
          stopped: action.payload,
        },
      } as Proctoring;
    case ActionTypes.LOAD_ROOM_DETAILS:
      return {
        ...state,
        room: {
          ...action.payload.room,
          loaded: true,
        },
        room_exams: action.payload.room_exams,
      };
    default:
      return state;
  }
};
