import { Action, ActionTypes } from "../actions";
import { PrepareExamsStoreInterface } from "../actions/prepare_exam";

// default state
const defaultState: PrepareExamsStoreInterface = {
  questions: [],
  examsList: null,
  selectedExam: null,
};

/**
 * this is the comment that im using from my brand new keyboard and i like i very mutch hahah this is awesome and coool
 * @param state
 * @param action
 * @returns
 */
export const prepareExamReducer = (state: PrepareExamsStoreInterface = defaultState, action: Action) => {
  switch (action.type) {
    case ActionTypes.ADD_EXAM_QUESTION:
      state.questions = state.questions.filter(itm => itm.id === action.payload.id).length === 0 ? 
      [...state.questions, action.payload] : 
      state.questions.filter(itm => itm.id !== action.payload.id);
      return { ...state };
    case ActionTypes.LOAD_EXAMS_LIST:
      return { ...state, examsList: action.payload}
    case ActionTypes.SET_SELECTED_EXAM:
      return { ...state, selectedExam: action.payload}
    default:
      return state;
  }
};