import React from "react";
import { connect } from "react-redux";
import { positionRequirementInterface } from ".";
import Table from "../../../components/Table/Table";
import { StoreState } from "../../../reducers";
import { DocumentReducerInterface } from "../../../reducers/document";
import { DocumentFieldsInterface } from "../../../shared/interfaces";

interface RequirementProps {
  requirement: positionRequirementInterface[];
  document: DocumentReducerInterface;
}

const ReviewRequirements = (props: RequirementProps) => {
  const getFieldById = (field_id: string): DocumentFieldsInterface => {
    let result: DocumentFieldsInterface[] = [];
    if (props.document.documentFields) {
      result = props.document.documentFields.filter(
        (item) => item._id === field_id
      );
    }
    return result[0];
  };

  const tableData = (): JSX.Element[] => {
    let data: positionRequirementInterface[] = props.requirement;
    if (data.length === 0) {
      return [
        <tr key={1} className="border-b border-gray-200 hover:bg-gray-100">
          <td className="p-3 text-center" colSpan={3}>
            No Requirement Found
          </td>
        </tr>,
      ];
    }

    return data.map((item, index) => (
      <tr key={index} className="border-b border-gray-200 hover:bg-gray-100">
        <td className="p-3 text-left whitespace-nowrap">{index + 1}</td>
        <td className="p-3 text-left">
          <span>
            {getFieldById(item.category_subcategory_data_id)
              ? getFieldById(item.category_subcategory_data_id).title
              : ""}
          </span>
        </td>
        <td className="p-3 text-left text-lg italic">
          {item.required ? (
            <span className="text-yellow-800">Required</span>
          ) : (
            "Optional"
          )}
        </td>
      </tr>
    ));
  };

  return (
    <div>
      <Table
        columns={[
          { columnTitle: "#", alignText: "left", padding: "p-3" },
          { columnTitle: "Document", alignText: "left", padding: "p-3" },
          { columnTitle: "Status", alignText: "left", padding: "p-3" },
          // { columnTitle: "", alignText: "left", padding: "p-3" },
        ]}
        rows={tableData}
        pagination={false}
        search={false}
        thColor="white"
      />
    </div>
  );
};

const mapStateToProps = ({
  document,
}: StoreState): {
  document: DocumentReducerInterface;
} => {
  return {
    document,
  };
};
export default connect(mapStateToProps)(ReviewRequirements);
