import { Action, ActionTypes } from "../actions";
import { QuestionBankStoreInterface } from "../actions/question";
import { QuestionInterface } from "../interfaces/question";

// default state
const defaultState: QuestionBankStoreInterface = {
  questions: null,
  selectedQuestion: null,
  uploadedQuestions: null,
};

/**
 * this is the comment that im using from my brand new keyboard and i like i very mutch hahah this is awesome and coool
 * @param state
 * @param action
 * @returns
 */
export const questionBankReducer = (state: QuestionBankStoreInterface = defaultState, action: Action) => {
  switch (action.type) {
    case ActionTypes.LOAD_QUESTION_BANK:
      if (state.questions !== null) {
        state.questions.length = 0;
      }
      state.questions = action.payload;
      return state;
    case ActionTypes.SET_SELECTED_QUESTION:
      return {...state, selectedQuestion: action.payload};
    case ActionTypes.SET_UPLOADED_QUESTIONS:
      return {...state, uploadedQuestions: action.payload};
    case ActionTypes.INCREMENT_QUESTION_BANK:
      return {...state, questions: state.questions === null ? [action.payload] : [...state.questions, action.payload]};
    case ActionTypes.UPDATE_QUESTION:
      let check = state.uploadedQuestions?.insertedData.filter(itm => itm.id !== action.payload.id);
      if (check === undefined) {
        return {...state};
      } else {
        if (state.uploadedQuestions !== null) {
          let newOne: QuestionInterface[] = [action.payload, ...check];
          state = {
            questions: state.questions,
            selectedQuestion: null,
            uploadedQuestions: {
              insertedCount: state.uploadedQuestions.insertedCount, 
              insertedData: newOne, 
              insertedIds: state.uploadedQuestions.insertedIds, 
              result: state.uploadedQuestions.result
            }
          }
          return {...state}
        } else {
          return {...state};
        }
      }
    default:
      return state;
  }
};