import axios from "axios";
import React, { Fragment, useEffect, useState } from "react";
import { FaFilter } from "react-icons/fa";
import {
  CandidatesInterface,
  CentersInterface,
  DistrictInterface,
  LocationRoomTableInterface,
  RoomsInterface,
} from "../../shared/interfaces";
import { API } from "../../utils/api";

interface FilterFormProps {
  roomTables: LocationRoomTableInterface[] | null;
  candidateList: CandidatesInterface[];
  distinctDistricts: LocationRoomTableInterface[] | null;
  thisDistrict: LocationRoomTableInterface | null;
  thisCenter: LocationRoomTableInterface | null;
  setThisDistrict: React.Dispatch<
    React.SetStateAction<LocationRoomTableInterface | null>
  >;
  filterData: (
    filterType:
      | "docCategory"
      | "docSubCategory"
      | "docStatus"
      | "DISTRICT"
      | "CENTER"
      | "ROOM",
    filterValue: string
  ) => void;
  setThisCenter: React.Dispatch<
    React.SetStateAction<LocationRoomTableInterface | null>
  >;
  setThisRoom: React.Dispatch<
    React.SetStateAction<LocationRoomTableInterface | null>
  >;
  loadingRequest: boolean;
}

const FilterForm: React.FC<FilterFormProps> = (props) => {
  const [district, setDistrict] = useState<DistrictInterface[] | null>(null);
  const loadDistrict = async () => {
    try {
      const res = await axios.get<DistrictInterface[]>(
        `${API.APPLICATION}/district`
      );
      setDistrict(res.data);
    } catch (error) {
      console.log({ code_error: error });
    }
  };

  useEffect(() => {
    if (!district) {
      loadDistrict();
    }
  }, [district]);

  const selectDistrict = (district_id: string) => {
    props.distinctDistricts &&
      props.setThisDistrict(
        props.distinctDistricts.filter(
          (district) => district.district_id === district_id
        )[0]
      );
    props.filterData("DISTRICT", district_id);
    props.setThisCenter(null);
    props.setThisRoom(null);
  };

  const selectCenter = (center_id: string) => {
    props.roomTables &&
      props.setThisCenter(
        props.roomTables.filter((center) => center.center_id === center_id)[0]
      );
    props.filterData("CENTER", center_id);
    props.setThisRoom(null);
  };

  const selectRoom = (room_id: string) => {
    props.roomTables &&
      props.setThisRoom(
        props.roomTables.filter((room) => room.room_id === room_id)[0]
      );
    props.filterData("ROOM", room_id);
  };

  const getCenterByDistrict = (
    district_id: string
  ): LocationRoomTableInterface[] => {
    let n: LocationRoomTableInterface[] = [];
    if (props.roomTables) {
      n = props.roomTables.filter(
        (element) => element.district_id === district_id
      );
    }

    let result: LocationRoomTableInterface[] = [],
      _ids: string[] = [];
    for (let i = 0; i < n.length; i++) {
      if (_ids.includes(n[i].center_id)) continue;
      _ids.push(n[i].center_id);
      result.push(n[i]);
    }

    return result;
  };

  const getRoomByCenter = (center_id: string): LocationRoomTableInterface[] => {
    let n: LocationRoomTableInterface[] = [];
    if (props.roomTables) {
      n = props.roomTables.filter((element) => element.center_id === center_id);
    }

    let result: LocationRoomTableInterface[] = [],
      _ids: string[] = [];
    for (let i = 0; i < n.length; i++) {
      if (_ids.includes(n[i].room_id)) continue;
      _ids.push(n[i].room_id);
      result.push(n[i]);
    }

    return result;
  };

  return (
    <Fragment>
      <div className="my-2 font-medium text-xl flex items-center gap-2">
        <FaFilter />
        Custom Filter by location
      </div>
      <div className="flex items-center md:flex-row flex-col gap-2">
        {props.distinctDistricts && (
          <div className="flex flex-col mb-3 md:w-1/3 w-full">
            <select
              className="w-full bg-white text-grey-darker border-2 border-gray-500 rounded h-10 px-2 focus:outline-none animate__animated animate__fadeIn"
              onChange={(e: React.ChangeEvent<HTMLSelectElement>) =>
                selectDistrict(e.target.value)
              }
              disabled={props.loadingRequest}
            >
              <option value="">Seleted District</option>
              {props.distinctDistricts && props.distinctDistricts.length > 0 ? (
                props.distinctDistricts.map((item, i) => (
                  <option key={i + item.district_id} value={item.district_id}>
                    {item.district_name}
                  </option>
                ))
              ) : (
                <option value="" disabled>
                  No District
                </option>
              )}
            </select>
          </div>
        )}

        {props.thisDistrict && (
          <div className="flex flex-col mb-3 md:w-1/3 w-full">
            <select
              className="w-full bg-white text-grey-darker border-2 border-gray-500 rounded h-10 px-2 focus:outline-none animate__animated animate__fadeIn"
              onChange={(e: React.ChangeEvent<HTMLSelectElement>) =>
                selectCenter(e.target.value)
              }
              disabled={props.loadingRequest}
            >
              <option value="">Select center</option>
              {props.thisDistrict &&
              getCenterByDistrict(props.thisDistrict.district_id).length > 0 ? (
                getCenterByDistrict(props.thisDistrict.district_id).map(
                  (item, i) => (
                    <option key={i + item.center_id} value={item.center_id}>
                      {item.center_name}
                    </option>
                  )
                )
              ) : (
                <option value="" disabled>
                  No center
                </option>
              )}
            </select>
          </div>
        )}

        {props.thisCenter && (
          <div className="flex flex-col mb-3 md:w-1/3 w-full">
            <select
              className="w-full bg-white text-grey-darker border-2 border-gray-500 rounded h-10 px-2 focus:outline-none animate__animated animate__fadeIn"
              onChange={(e: React.ChangeEvent<HTMLSelectElement>) =>
                selectRoom(e.target.value)
              }
              disabled={props.loadingRequest}
            >
              <option value="">Select room</option>
              {props.thisCenter &&
              getRoomByCenter(props.thisCenter.center_id).length > 0 ? (
                getRoomByCenter(props.thisCenter.center_id).map((item, i) => (
                  <option key={i + item.room_id} value={item.room_id}>
                    {item.room_name}
                  </option>
                ))
              ) : (
                <option value="" disabled>
                  No Room
                </option>
              )}
            </select>
          </div>
        )}
      </div>
    </Fragment>
  );
};

export default FilterForm;
