import axios from "axios";
import React, { useState } from "react";
import { AiFillDelete } from "react-icons/ai";
import { BiDetail } from "react-icons/bi";
import { IoMdEye, IoMdEyeOff } from "react-icons/io";
import { MdAdd, MdClear, MdCreate } from "react-icons/md";
import { connect } from "react-redux";
import { deleteDocumentSubCategory } from "../../actions/document";
import ActionConfirm from "../../components/ActionConfirm";
import {
  DocumentAttributesInterface,
  DocumentCategoryInterface,
  DocumentSubCategoryInterface,
} from "../../shared/interfaces";
import { API } from "../../utils/api";

interface AppProps {
  thisCategory: DocumentCategoryInterface;
  actionType: string;
  deleteDocumentSubCategory: Function;
  documentSubCategory: DocumentSubCategoryInterface[] | null;
  feedBack:
    | {
        type: "success" | "danger" | "warning" | "";
        message: string;
      }
    | undefined;
  viewFields: "" | "SUB_CATEGORY" | "CATEGORY";
  subCategoryId: string;
  setViewFields: React.Dispatch<
    React.SetStateAction<"" | "SUB_CATEGORY" | "CATEGORY">
  >;
  setSubCategoryId: React.Dispatch<React.SetStateAction<string>>;
  setSubCategoryTitle: React.Dispatch<React.SetStateAction<string>>;
  setSubCategoryDescription: React.Dispatch<React.SetStateAction<string>>;
  setNullId: React.Dispatch<React.SetStateAction<string>>;
  setActionType: React.Dispatch<
    React.SetStateAction<
      | ""
      | "CREATE_SUB"
      | "CREATE_CAT"
      | "UPDATE_SUB"
      | "UPDATE_CAT"
      | "CREATE_FIELD"
      | "UPDATE_FIELD"
    >
  >;
  setFeedBack: React.Dispatch<
    React.SetStateAction<
      | {
          type: "success" | "danger" | "warning" | "";
          message: string;
        }
      | undefined
    >
  >;
}

const SubCategory: React.FC<AppProps> = ({
  thisCategory,
  actionType,
  viewFields,
  subCategoryId,
  documentSubCategory,
  deleteDocumentSubCategory,
  setViewFields,
  setActionType,
  setSubCategoryId,
  setSubCategoryTitle,
  setSubCategoryDescription,
  setNullId,
  setFeedBack,
}) => {
  const [loading, setLoadind] = useState<boolean>(false);
  const sub_cats = documentSubCategory
    ? documentSubCategory.filter(
        (sub) => sub.category_id === thisCategory.user_detail_category_id
      )
    : [];
  const [deleteAction, setDeleteAction] = useState<
    boolean | "COMFIRMED" | "DECLINED"
  >(false);
  const handleDelete = async (subCategoryId: string) => {
    setFeedBack({ type: "", message: "" });
    if (
      window.confirm("Are you sure do you want to delete this sub-category?")
    ) {
      setDeleteAction(true);
      if (deleteAction === "DECLINED") {
        return console.log("Delete:", deleteAction);
      }
      console.log("Delete__:", deleteAction);
      return setDeleteAction(false);
      setLoadind(true);
      try {
        const res = await axios.delete<DocumentSubCategoryInterface>(
          `${API.AUTH}/detailsubcategory/${subCategoryId}`
        );
        if (res.data) {
          deleteDocumentSubCategory(subCategoryId);
          setFeedBack({
            type: "success",
            message: `Sub Category has been deleted successfully.`,
          });
          setLoadind(false);
        }
      } catch (error) {
        console.log({ doc_SUB_category_error: error });
        setLoadind(false);
      }
    }
  };

  return (
    <div>
      {/* user details about */}
      <div className="border-b px-2 py-3 flex justify-between gap-4">
        <h2 className="text-3xl font-semibold text-gray-800">
          Type of {thisCategory.title}
        </h2>
        <button
          type="button"
          className="btn mb-1 border-2 border-blue-500 bg-blue-500 hover:bg-white
        text-white rounded-md flex items-center font-bold hover:text-gray-700 focus:outline-none"
          onClick={() => setActionType("CREATE_SUB")}
          disabled={actionType === "CREATE_SUB" || actionType === "UPDATE_SUB"}
        >
          <MdAdd className="inline-block mr-1 text-xl font-bold" /> Type of{" "}
          {thisCategory.title}
        </button>
      </div>
      {sub_cats.length ? (
        <div>
          {thisCategory.fields.includes(
            DocumentAttributesInterface.category_subcategory_data
          ) && (
            <div className="flex justify-between items-start p-2 my-3 rounded-lg border border-gray-100 shadow-md">
              <div className="flex items-center">
                <div className="ml-2">
                  <div className="text-xl font-semibold text-gray-600">ALL</div>
                  <div className="text-sm font-light text-gray-500">
                    This default sub-category is for defining field(s) to be
                    used on all sub-categories
                  </div>
                </div>
              </div>
              <div className="flex items-center gap-2">
                {thisCategory.fields.includes(
                  DocumentAttributesInterface.category_subcategory_data
                ) && (
                  <button
                    title="View Fields"
                    className={`bg-gray-50 hover:bg-blue-100 rounded-full px-1 py-1 focus:outline-none`}
                    onClick={() => {
                      setNullId(thisCategory.user_detail_category_id);
                      setSubCategoryId("null");
                      setSubCategoryTitle("Default Subcategory");
                      setSubCategoryDescription(
                        "This default sub-category is for defining field(s) to be used on all sub-categories"
                      );
                      setViewFields("SUB_CATEGORY");
                    }}
                  >
                    {viewFields === "SUB_CATEGORY" ? (
                      <IoMdEyeOff className="text-3xl text-gray-700" />
                    ) : (
                      <IoMdEye className="text-3xl text-gray-700" />
                    )}
                  </button>
                )}
              </div>
            </div>
          )}
          {sub_cats.map((sub_category, i) => (
            <div
              key={i}
              className="flex justify-between items-start p-2 my-3 rounded-lg border border-gray-100 shadow-md"
            >
              <div className="flex items-center">
                <div className="ml-2">
                  <div className="text-xl font-semibold text-gray-600">
                    {sub_category.title}
                  </div>
                  <div className="text-sm font-light text-gray-500">
                    {sub_category.description}
                  </div>
                </div>
              </div>
              <div className="flex items-center gap-2">
                {thisCategory.fields.includes(
                  DocumentAttributesInterface.category_subcategory_data
                ) && (
                  <button
                    title="View Fields"
                    className={`bg-gray-50 hover:bg-blue-100 rounded-full px-1 py-1 focus:outline-none`}
                    onClick={() => {
                      setSubCategoryId(sub_category.user_detail_subcategory_id);
                      setSubCategoryTitle(sub_category.title);
                      setSubCategoryDescription(sub_category.description);
                      viewFields === "SUB_CATEGORY" &&
                      subCategoryId === sub_category.user_detail_subcategory_id
                        ? setViewFields("")
                        : setViewFields("SUB_CATEGORY");
                    }}
                  >
                    {viewFields === "SUB_CATEGORY" ? (
                      <IoMdEyeOff className="text-3xl text-gray-700" />
                    ) : (
                      <IoMdEye className="text-3xl text-gray-700" />
                    )}
                  </button>
                )}
                <button
                  title="Update Sub-Category"
                  className="bg-gray-50 hover:bg-blue-100 rounded-full px-1 py-1 focus:outline-none"
                  onClick={() => {
                    setSubCategoryId(sub_category.user_detail_subcategory_id);
                    setSubCategoryTitle(sub_category.title);
                    setSubCategoryDescription(sub_category.description);
                    setActionType("UPDATE_SUB");
                  }}
                >
                  <MdCreate className="text-3xl text-blue-700" />
                </button>
                <button
                  title="Delete Sub-Category"
                  className="bg-gray-50 hover:bg-blue-100 rounded-full px-1 py-1 focus:outline-none"
                  onClick={() =>
                    handleDelete(sub_category.user_detail_subcategory_id)
                  }
                  disabled={loading}
                >
                  <AiFillDelete className="text-3xl text-red-700" />
                </button>
              </div>
            </div>
          ))}
        </div>
      ) : (
        <div className="text-center text-3xl text-gray-500 font-medium py-8">
          <BiDetail className="w-40 h-40 mx-auto animate__pulse" />
          No Sub Categories Available
        </div>
      )}
      {/* <!-- End of profile tab --> */}
      {deleteAction === true && (
        <ActionConfirm setDeleteAction={setDeleteAction} />
      )}
    </div>
  );
};

export default connect(null, { deleteDocumentSubCategory })(SubCategory);
