import React, { Fragment } from "react";
import { RoomsInterface } from "../../shared/interfaces";

interface RoomFormProps {
  roomNumber: number;
  roomCapacity: number;
  thisRoom: RoomsInterface | null;
  loadingRequest: boolean;
  setRoomNumber: React.Dispatch<React.SetStateAction<number>>;
  setRoomCapacity: React.Dispatch<React.SetStateAction<number>>;
  setThisRoom: React.Dispatch<React.SetStateAction<RoomsInterface | null>>;
  setActionTypes: React.Dispatch<
    React.SetStateAction<
      | ""
      | "CREATE_PROVINCE"
      | "UPDATE_PROVINCE"
      | "CREATE_DISTRICT"
      | "UPDATE_DISTRICT"
      | "CREATE_CENTER"
      | "UPDATE_CENTER"
      | "CREATE_ROOM"
      | "UPDATE_ROOM"
    >
  >;
  handleSubmitRoom: () => Promise<void>;
}

const RoomForm = (props: RoomFormProps) => {
  return (
    <Fragment>
      <div className="py-4 text-base leading-6 space-y-4 text-gray-700 sm:text-lg sm:leading-7">
        <div className="flex flex-col">
          <label>Room</label>
          <input
            type="number"
            className="text-left input-white w-20 inline-block border-gray-500 border-2"
            placeholder="Room number"
            value={props.roomNumber}
            onChange={(e) => props.setRoomNumber(parseInt(e.target.value))}
            disabled={props.loadingRequest}
          />
        </div>
        <div className="flex flex-col">
          <label>Seats Capacity</label>
          <input
            type="number"
            className="text-left input-white w-20 inline-block border-gray-500 border-2"
            placeholder="Room capacity"
            value={props.roomCapacity}
            onChange={(e) => props.setRoomCapacity(parseInt(e.target.value))}
            disabled={props.loadingRequest}
          />
        </div>
      </div>
      <div className="pt-4 flex justify-between">
        <button
          onClick={() => {
            props.setActionTypes("");
            props.setThisRoom(null);
            props.setRoomCapacity(0);
            props.setRoomNumber(0);
          }}
          className={`btn bg-gray-400 text-white hover:bg-gray-600`}
          type="button"
          disabled={props.loadingRequest}
        >
          Exit
        </button>
        <button
          className="btn bg-primary text-white hover:bg-blue-600"
          type="button"
          onClick={() => props.handleSubmitRoom()}
          disabled={props.loadingRequest}
        >
          {props.loadingRequest ? "Loading..." : "Save"}
        </button>
      </div>
    </Fragment>
  );
};

export default RoomForm;
